.time {
  display: flex;
  flex-direction: row;
  transform: translateZ(20px);
  transform:
    translateZ(20px)
    rotateX(calc(var(--mouse-y) * 45deg))
    rotateY(calc(var(--mouse-x) * 45deg))
}

.digit {
  height: 20vmin;
  width: 10vmin;
  transform-style: preserve-3d;
  perspective: 1000px;
  margin: 0 1rem;
}

.line {
  position: absolute;
  transform-style: preserve-3d;
  perspective: 1000px;
  color: pink;
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #121212;
  transform-style: preserve-3d;
  perspective: 1000px;
}

body, html {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
